.container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    height: 100%;
    -ms-flex: 1 1 0%;
        flex: 1 1 0%;
    padding-top: 20px;
    padding-bottom: 20px;
}

.loading-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    height: 100%;
}

.list-wrapper {
    margin-top: 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    width: 100%;
}

.content-header {
    font-size: 20px;
    margin-bottom: 10px;
    padding-left: 10px;
}

.content-block {
    width: 20%;
    min-width: 200px;
    -webkit-transition: all 0.1s;
    -o-transition: all 0.1s;
    transition: all 0.1s;
    max-height: calc(95vh - 65px);
    overflow: auto;
    margin-left: 20px;
    position: fixed;
}

.text-block {
    width: 800px;
    background-color: #fff;
    -webkit-box-shadow: 0px 0px 10px 0px rgba(55, 71, 95, 0.1);
            box-shadow: 0px 0px 10px 0px rgba(55, 71, 95, 0.1);
    padding: 20px;
    -webkit-transition: all 0.1s;
    -o-transition: all 0.1s;
    transition: all 0.1s;
    margin-left: 330px;
}

.document-info {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -ms-flex-direction: column;
        flex-direction: column;
    border-bottom: 1px solid rgba(209, 215, 221, 0.5);
    -webkit-transition: all 0.5ms;
    -o-transition: all 0.5ms;
    transition: all 0.5ms;
    background-color: #fff;
}

.content {
    display: -ms-flexbox;
    display: flex;
    /*width: 100%;*/
    -ms-flex-direction: column;
        flex-direction: column;
    -webkit-transition: all 0.5ms;
    -o-transition: all 0.5ms;
    transition: all 0.5ms;
    margin-left: 10px;
}

.container:hover {
    -webkit-box-shadow: 0px 0px 0px 0px rgba(55, 71, 95, 0.1);
            box-shadow: 0px 0px 0px 0px rgba(55, 71, 95, 0.1)
}

.card-picture-block {
    width: 100%;
}

.card-picture {
    width: 100%;
    /*height: 40px;*/
    display: block;
}

.card-description {
    margin-right: 5px;
    color: #37475f;
}

.card-name {
    font-size: 16px;
    padding: 10px;
    font-weight: 500;
    color: #37475f;
    border-bottom: 1px solid #f0f8ff;
}

.card-document-description-wrapper {
    /*width: 35%;*/
    color: #37475f;
    margin-bottom: 10px;
    padding-top: 5px;
    padding-left: 10px;
    padding-right: 10px;
}

.section-container {
    padding-left: 5px;
    padding-right: 5px;
}

.section-container-hightlighted {
    border: 1px solid rgba(209, 215, 221, 1);
    padding-left: 5px;
    padding-right: 5px;
}

.empty-content-message {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    width: 100%;
    font-size: 20px;
    font-weight: 300;
    color: #808b9b;
}



