.container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-pack: center;
        justify-content: center;
    height: 100%;
    /*padding-top: 100px;*/
    -ms-flex: 1 1 0%;
        flex: 1 1 0%;
    padding-top: 50px;
}

.content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}
.form-block {
    width: 400px;
    height: 100%;
}

.users-block {
    width: 400px;
    margin-left: 10px;
    overflow:auto;
}

.user-block-title {
    margin-bottom: 5px;
    color: #8691a2;
    font-weight: 600;
    font-size: 14px;
}

.user-block-content {
    max-height: 400px;
    background-color: #FFF;
    padding: 5px;
    border: 1px solid #dedede;
    border-radius: 5px;
    width: 100%;
    height: 94%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    overflow: auto;
}

.users-block-content::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 5px;
	background-color: #F5F5F5;
}

.users-block-content::-webkit-scrollbar {
    width: 5px;
    background-color: #F5F5F5;
}

.users-block-content::-webkit-scrollbar-thumb {
    border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #555;
}

.user-card {
    margin: 3px;
    border: 1px solid #dedede;
    border-radius: 5px;
    height: 190px;
}

.picture {
    width: 120px;
}

.error-block {
    color: red;
    font-size: 8px;
    margin-left: 10px;
}

.email {
    font-weight: 600;
    font-size: 10px;
    margin-left: 3px;
}
.name {
    font-weight: 600;
    font-size: 10px;
    margin-left: 3px;
}
.position {
    font-weight: 600;
    font-size: 10px;
    margin-left: 3px;
}

.label {
    color: #8691a2 !important;
}

.title {
    color: #8691a2;
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(134, 145, 162, 0.3);
    margin-bottom: 30px;
    margin-top: 10px;
    text-align: center;
    font-size: 25px;
    font-weight: 300;
}

.form {
    width: 400px;
}

.document-input {
    margin-bottom: 20px;
    color: rgba(0, 0, 0, 0.6) !important;
}

.name-input {
    margin-bottom: 20px;
}

.date-input {
    /*margin-bottom: 20px;*/
}

.name-input input {
    color: rgba(0, 0, 0, 0.6) !important;
}

.groups-input {
    margin-top: 0px !important;
    margin-bottom: 20px;
    width: 100%;
}

.description-input {
    margin-bottom: 20px;
    color: rgba(0, 0, 0, 0.6) !important;
}

.groups-input {
    margin-top: 10px;
    width: 100%;
}

.create-btn {
    margin: 30px auto 0 !important;
    display: block !important;
    font-weight: 400 !important;
    background-color: #4fa1ff !important;
}



