.container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-pack: center;
        justify-content: center;
    height: 100%;
    padding-top: 50px;
    -ms-flex: 1 1 0%;
        flex: 1 1 0%;
}

.content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}
.form-block {
    width: 400px;
    height: 100%;
}

.users-block {
    width: 400px;
    margin-left: 10px;
    overflow:auto;
}

.user-block-title {
    margin-bottom: 5px;
    color: #8691a2;
    font-weight: 600;
    font-size: 14px;
}

.training-kit-input {

}

.user-block-content {
    max-height: 460px;
    padding: 5px;
    width: 100%;
    height: 94.9%;
    overflow: auto;
}

.users-block-content::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 5px;
	background-color: #F5F5F5;
}

.users-block-content::-webkit-scrollbar {
    width: 5px;
    background-color: #F5F5F5;
}

.users-block-content::-webkit-scrollbar-thumb {
    border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #555;
}

.user-card {
    /*margin: 3px;*/
    margin-bottom: 5px;
    -webkit-box-shadow: 0 0 20px 0 rgba(55,71,95,.1);
            box-shadow: 0 0 20px 0 rgba(55,71,95,.1);
    border-radius: 5px;
    /*height: 100px;*/
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
    padding: 10px 20px;
    background-color: #fff;
}

.picture {
    width: 80px;
    height: 80px;
    border-radius: 50%;
}

.error-block {
    color: red;
    font-size: 8px;
    margin-left: 10px;
}

.email {
    font-size: 12px;
    color: #7f8b9b;
}
.name {
    font-size: 16px;
}
.position {
    font-size: 14px;
}

.position-label {
    margin-right: 4px;
}

.label {
    color: #8691a2 !important;
}

.title {
    color: #8691a2;
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(134, 145, 162, 0.3);
    margin-bottom: 30px;
    margin-top: 10px;
    text-align: center;
    font-size: 25px;
    font-weight: 300;
}

.form {
    width: 400px;
}

.document-input {
    margin-bottom: 20px;
    color: rgba(0, 0, 0, 0.6) !important;
}

.name-input {
    margin-bottom: 20px;
}

.date-input {
    /*margin-bottom: 20px;*/
}

.name-input input {
    color: rgba(0, 0, 0, 0.6) !important;
}

.description-input {
    margin-bottom: 20px;
    color: rgba(0, 0, 0, 0.6) !important;
}

.groups-input {
    /*margin-top: 10px;*/
    width: 100%;
}

.create-btn {
    margin: 30px auto 0 !important;
    display: block !important;
    font-weight: 400 !important;
    background-color: #4fa1ff !important;
}

.user-info {
    margin-left: 15px;
}

.no-selected-groups-message {
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    color: #7f8b9b;
    font-size: 16px;
}



