.container {
    -webkit-transition: all 0.4s ease-out;
    -o-transition: all 0.4s ease-out;
    transition: all 0.4s ease-out;
    width: 450px;
    height: 160px;
    display: -ms-flexbox;
    display: flex;
    background-color: #fff;
    -webkit-box-shadow: 0 0 20px 0 rgba(55,71,95,.1);
            box-shadow: 0 0 20px 0 rgba(55,71,95,.1);
    border-radius: 5px;
    margin-bottom: 50px;
}

.container:hover {
    cursor: pointer;
    -webkit-box-shadow: 0 0 20px 0 rgba(55, 71, 95, 0.18);
            box-shadow: 0 0 20px 0 rgba(55, 71, 95, 0.18);
}

.container-extended {
    -webkit-transition: all 0.4s ease-out;
    -o-transition: all 0.4s ease-out;
    transition: all 0.4s ease-out;
    width: 500px;
    height: 200px;
    display: -ms-flexbox;
    display: flex;
    background-color: #fff;
    -webkit-box-shadow: 0 0 20px 0 rgba(55,71,95,.1);
            box-shadow: 0 0 20px 0 rgba(55,71,95,.1);
    border-radius: 5px;
    margin-bottom: 50px;
}

.container-extended:hover {
    cursor: pointer;
    -webkit-box-shadow: 0 0 20px 0 rgba(55, 71, 95, 0.18);
            box-shadow: 0 0 20px 0 rgba(55, 71, 95, 0.18);
}

.image-container {

}

.image {
    width: 200px;
    height: 100%;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.content-container {
    padding: 10px 5px 10px 10px;
}

.title {
    font-size: 16px;
    line-height: normal;
    font-weight: 500;
    margin-bottom: 10px;
}

.subtitle {
    font-size: 16px;
    line-height: normal;
    font-size: 14px;
    color: #b3b3b3;
    height: 35px;
}

.subtitle-short {
    font-size: 16px;
    line-height: normal;
    font-size: 14px;
    color: #b3b3b3;
}

.info-raw {
    margin-top: 7px;
}

.info-caption {
    margin-right: 5px;
    color: #b3b3b3;
    font-weight: 500;
}

.info-value {

}

.link {
    color: #4ea1ff;
    margin-left: 5px;
}

.link:hover {
    color: #4ea1ff;
    border-bottom: 1px solid #4ea1ff;
}

.tags-wrapper {
    margin-top: 10px;
}

.tag-label {
    margin-bottom: 5px !important;
}

.hidden-tags-number {
    margin-left: 5px;
    color: #4fa1ff;
}



